import {
  Facebook,
  Instagram,
  MailOutline,
  Phone,
  Pinterest,
  Room,
  Twitter,
} from "@material-ui/icons";
import { Link, Route } from "react-router-dom";
import styled from "styled-components";
import ProductList from "../pages/ProductList";
import { mobile } from "../responsive";

const Container = styled.div`
    display: flex;
    ${mobile({ flexDirection: "column" })}
  `;

const Left = styled.div`
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 20px;
  `;

const Logo = styled.h1``;

const Desc = styled.p`
    margin: 20px 0px;
  `;

const SocialContainer = styled.div`
    display: flex;
  `;

const SocialIcon = styled.div`
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: white;
    background-color: #${(props) => props.color};
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
  `;

const Center = styled.div`
    flex: 1;
    padding: 20px;
    ${mobile({ display: "none" })}
  `;

const Title = styled.h3`
    margin-bottom: 30px;
  `;

const List = styled.ul`
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
  `;

const ListItem = styled.li`
    width: 50%;
    margin-bottom: 10px;
  `;

const Right = styled.div`
    flex: 1;
    padding: 20px;
    ${mobile({ backgroundColor: "#fff8f8" })}
  
  `;

const ContactItem = styled.div`
    margin-bottom: 20px;
    display: flex;
    align-items: center;
  `;

const Payment = styled.img`
      width: 50%;
  `;

const Footer = () => {
  return (
    <Container>
      <Left>
        <Logo>E-Com</Logo>
        <Desc>
          There are many variations of passages of Lorem Ipsum available, but
          the majority have suffered alteration in some form, by injected
          humour, or randomised words which dont look even slightly believable.
        </Desc>
        <SocialContainer>
          <SocialIcon color="3B5999">
            <Facebook />
          </SocialIcon>
          <SocialIcon color="E4405F">
            <Instagram />
          </SocialIcon>
          <SocialIcon color="55ACEE">
            <Twitter />
          </SocialIcon>
          <SocialIcon color="E60023">
            <Pinterest />
          </SocialIcon>
        </SocialContainer>
      </Left>
      <Center>
        <Title>Useful Links</Title>
        <List>
          <Link to="/" style={{ width: "50%",textDecoration:"none"}}>

            <ListItem>Home</ListItem>
          </Link>

          <Link to="/cart" style={{ width: "50%",textDecoration:"none" }}>
            <ListItem>Cart</ListItem>
          </Link>

          <Link to="/products/:category" style={{ width: "50%",textDecoration:"none" }}>
              <ListItem>Categories</ListItem>
          </Link>

          <Link to="/product/:id" style={{ width: "50%",textDecoration:"none" }}>
            <ListItem>Products</ListItem>
          </Link>

          <Link to="/register" style={{ width: "50%" ,textDecoration:"none"}}>
            <ListItem>Register</ListItem>
          </Link>

          <Link to="/login" style={{ width: "50%" , textDecoration:"none" }}>
            <ListItem>Login</ListItem>
          </Link>

    
       

        </List>
      </Center>
      <Right>
        <Title>Contact</Title>
        <ContactItem>
          <Room style={{ marginRight: "10px" }} /> 888 Marine Drive , South Bombay 007
        </ContactItem>
        <ContactItem>
          <Phone style={{ marginRight: "10px" }} /> +91 749796796
        </ContactItem>
        <ContactItem>
          <MailOutline style={{ marginRight: "10px" }} /> contactUs@E-com
        </ContactItem>
        <Payment src="https://i.ibb.co/Qfvn4z6/payment.png" />
      </Right>
    </Container>
  );
};

export default Footer;